<template>
	<div class="memo-wrapper">
		<div class="memo">
			<div class="header">
				<div class="top">
					<span class="title">{{item.title}}</span>
					<div class="tool">
						<span class="add" @click="edit">编辑</span>
						<span class="delete" @click="deletes">删除</span>
					</div>
				</div>
				<div class="bottom">
					<div class="time">{{time}}</div>
					<div class="sort">分类：{{item.label}}</div>
				</div>
			</div>
			<!-- markdown -->
			<div class="content" v-if="item.type == 1">{{item.content}}</div>
			<!-- poor -->
			<div class="poor" v-if="item.type == 2">poor</div>
		</div>

	</div>

</template>

<script>
	import {changeTime} from '../main.js'
	import {mapState,mapMutations} from 'vuex'
	import {postTable,getTable} from '../http/func.js'
	export default {
		data() {
			return {

			}
		},
		props: ['item'],
		methods: {
			//删除
			deletes() {
				console.log(this.item.time)
				this.deleteMemoItem(this.item.time)
				postTable('/delete/',{time:this.item.time}).then((res)=>{
					
				}).catch((err)=>{
					
				})
			},
			//编辑
			edit() {
				this.setIsNew(false) //表示这是编辑
				this.setShowEdit(true)
				this.setItemContent(this.item)
				console.log(this.itemContent)
				// getTable('/init').then((res)=>{
					
				// })
			},
			//vuex中的方法
			...mapMutations([
				'setMainType',
				'addMemoItem',
				'setShowEdit',
				'deleteMemoItem',
				'setItemContent',
				'setIsNew'
			])
		},
		computed: {
			time: function() {
				return changeTime(this.item.time)
			},
			...mapState([
				'isShowEdit',
				'mainType',
				'memoItem',
				'itemContent',
				'isNew'
			])
		}
	}
</script>

<style scoped lang="scss">
	@import '../assets/style/sass.scss';

	.memo-wrapper {
		padding: 5px;
		box-sizing: border-box;
		float: left;

		.memo {
			background-color: $bg_color;
			border: 1px solid $border_color;
			border-radius: 5px;
			padding: 10px;
			height: 300px;

			.header {
				.top {
					.title{
						height: 18.5px;
						padding: 4px 0;
						border-bottom: 1px solid #bdbdbd;
					}
					.tool {
						float: right;
						margin-top: 2px;
						.delete{
							color: #dd001b;
						}
						&>span:hover{
							cursor: pointer;
						}
					}
				}
				.bottom{
					.time{
						font-size: 0.75rem;
						padding: 5px 0 3px;
					}
				}
			}

			.content {
				word-wrap: break-word;
				word-break: normal;
				height: 242.4px;
				overflow-y: auto;
			}
		}
	}
</style>



