<template>
	<div class="wrapper">
		<!-- 头部 -->
		<Headers class="_header"></Headers>
		<!-- 备忘录 -->
		<div class="content">
			<div class="container">
				<memoItem v-for="item in this.$store.state.memoItem" :item="item" :key="item.time"></memoItem>
			</div>
		</div>
		<!-- 编辑页面 -->
		<memoEdit v-show="this.$store.state.isShowEdit" class="Edit"></memoEdit>
		<!-- 登陆 -->
		<Login v-show="this.$store.state.isLogin" class="login"></Login>
		<!-- 遮罩 -->
		<div class="mark" v-show="this.$store.state.isShowEdit ||this.$store.state.isLogin"></div>
		<!-- 备案号 -->
		<Record class="record"></Record>
	</div>
</template>

<script>
	import memoEdit from './memoEdit.vue'
	import memoItem from './memoItem.vue'
	import Headers from './Header.vue'
	import Record from './Record.vue'
	import Login from './Login.vue'
	import {
		mapState,
		mapMutations
	} from 'vuex'
	import {postTable,getTable} from '../http/func.js'

	export default {
		name: 'Home',
		components: {
			memoEdit,
			memoItem,
			Headers,
			Record,
			Login
		},
		data() {
			return {
				// memoItemData:[]
			}
		},
		methods: {
			...mapMutations([
				'setMainType',
				'addMemoItem',
				'setShowEdit',
				'setItemContent',
				'sortMomeItem',
				'initItem'
			]),
			//获取用户表格
			init(){
				if(this.userName){
					postTable('/init/',{userName:this.userName}).then((res)=>{
						if(res.data.userData){
							this.initItem(res.data.userData)
						}else{
							this.initItem('')
						}
							console.log(res)
					}).catch((err)=>{
					})
				}else{
					console.log('没有用户名')
				}
			}

		},
		created() {



		},
		mounted() {
			console.log(this.memoItem)
			//默认是降序
			this.sortMomeItem(false)
			this.init()
			
			console.log(this.$store.state.userName)
		},
		watch: {},
		computed: {
			...mapState([
				'isShowEdit',
				'mainType', //编辑模块显示markdown还是poor
				'memoItem',
				'itemContent',
				'isNew',
				'userName'
			]),

			memoItemData: function() {
				console.log('计算属性', JSON.parse(localStorage.memoItem))
				var data = JSON.parse(localStorage.memoItem)
				data.forEach((item)=>{
					if(!item.title){
						item.title == ' '
					}
				})
				return JSON.parse(localStorage.memoItem)
			}
		},
		watch: {

		}

	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="scoped" lang="scss">
	@import '../assets/style/sass.scss';

	.wrapper {
		._header {
			position: fixed;
			width: 100%;
		}

		overflow: scroll; //BFC；撑开高度

		// 隐藏滚动条
		&::-webkit-scrollbar {
			display: none;
		}

		//IE 10+
		& {
			-ms-overflow-style: none;
		}

		//Firefox
		& {
			overflow: -moz-scrollbars-none;
		}

		height: 100%;

		.content:after {
			content: '';
			clear: both;
		}

		.content {
			margin-top: 60px;
			// height: 90.5%;      //是视口高度的90.5%;有BFC撑开高度之后就不要用固定

			overflow: hidden; //BFC；撑开高度

			.record {
				clear: both;
			}
		}
	}

	.mark {
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: #222;
		opacity: 0.5;

	}

	.Edit {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
		z-index: 99;
	}
	
	.login{
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
		width: 500px;
		height: 250px;
		background-color: #fff;
		z-index: 99;
	}


	/*0-768px以上宽度container为100%*/
	.container {
		margin-right: auto;
		margin-left: auto;
	}


	/*768-992px以上宽度container为750px*/
	@media (min-width: 768px) {
		.container {
			width: 750px;
		}
	}

	/*992-1200px以上宽度container为970px*/
	@media (min-width: 992px) {
		.container {
			width: 970px;
		}
	}

	/*1200px以上宽度container为1170px*/
	@media (min-width: 1200px) {
		.container {
			width: 1170px;
		}
	}

	.active {
		background: #e7e7e7;
	}
</style>



