<template>
	<div class="wrappers">
		<div class="content">
			<div class="header">
				<div class="left">
					<!-- 标题 -->
					<input type="text" class="title" placeholder="标题" v-model="itemContent.title">
					<!-- 下拉框 -->
					<div class="btn-group btn-group-sm down_memu">
						<button type="button" class="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true"
						 aria-expanded="false" v-model="itemContent.label">
							{{type}} <span class="caret"></span>
						</button>
						<ul class="threeChange dropdown-menu" style="min-width:40px;left:-5px ;">
							<li @click="type = '工作'"><a href="#">工作</a></li>
							<li @click="type = '学习'"><a href="#">学习</a></li>
							<li @click="type = '生活'"><a href="#">生活</a></li>
						</ul>
					</div>
				</div>
				<div class="right">
					<!-- 保存 -->
					<span class="glyphicon glyphicon-floppy-disk" @click="save"></span>
					<span @click="close" class="glyphicon glyphicon-remove"></span>
				</div>
			</div>
			<!-- markdown -->
			<div class="main" v-if="this.$store.state.mainType == 1">
				<textarea class="text-content form-control" v-model="itemContent.content"></textarea>
			</div>
			<!-- poor -->
			<div class="main" v-if="this.$store.state.mainType == 2">
				<div class="canvas-wrapper">
					<ul class="doodle-colors">
						<li data-color="black"></li>
						<li data-color="green"></li>
						<li data-color="yellow"></li>
						<li data-color="red"></li>
						<li data-color="white"></li>
					</ul>
					<ul class="doodle-controllers">
						<li class="undo"></li>
						<li class="redo"></li>
						<li class="clear"></li>
					</ul>
					<canvas class="doodle-content" width='260' height='260'></canvas>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {
		changeTime
	} from '../main.js'
	import {
		mapState,
		mapMutations
	} from 'vuex'
	import {
		postTable,
		getTable
	} from '../http/func.js'
	export default {
		data() {
			return {
				type: '工作',
				memoData: [], //遍历用的数组
				// createData: {
				//   type: '', //markdown? poor?
				//   title: '',
				//   label: '', //标签
				//   time: '', //创建时间
				//   content: '' //内容
				// }
			}
		},
		methods: {
			...mapMutations([
				'setMainType',
				'addMemoItem',
				'setShowEdit',
				'setItemContent',
				'setIsNew',
				'setLogin'
			]),
			close() {
				this.setShowEdit(false)
			},
			//保存
			save() {
				//如果是新建
				if (this.isNew) {
					this.itemContent.time = new Date().getTime()
				}

				this.itemContent.label = this.type //标签
				this.itemContent.type = this.mainType //memo类型
				console.log(this.itemContent)
				this.addMemoItem(this.itemContent)

				//传入服务器
				if (this.userName) {
					this.itemContent.userName = this.userName
					this.itemContent.userName = this.isNew
					postTable('/add/', {
						itemData: this.itemContent
					}).then((res) => {

					}).catch((err) => {

					})
				} else {
					console.log('无用户名,未登录?')
				}

				console.log(JSON.parse(localStorage.memoItem))
				//关闭
				this.setShowEdit(false)

			}
		},
		created() {

		},
		watch: {
			mainType: function() {
				console.log(this.mainType)
			}
		},
		computed: {
			...mapState([
				'isShowEdit',
				'mainType', //编辑模块显示markdown还是poor
				'memoItem',
				'itemContent',
				'isNew',
				'isLogin',
				'userName'
			])
		},

	}
</script>

<style lang="scss" scoped="scoped">
	@import 'bootstrap/dist/css/bootstrap.min.css';
	@import '../assets/style/sass.scss';

	.wrappers {

		.content {
			padding: 10px;
			background-color: #fff;
			width: 280px;
			height: 350px;
			box-shadow: 0 0 5px #fff;
			border-radius: 5px;

			.header {
				position: relative;

				.right {
					position: absolute;
					right: 0;
					top: 6px;

					span {
						padding: 2px 3px;
					}

					&>span:hover {
						cursor: pointer;
					}
				}

				.left {

					.title {
						font-size: 14px;
						font-weight: 600;
						width: 50%;
						border: 1px solid #ccc;
						border-radius: 5px;
						padding: 5px;
					}

					.title:focus {
						outline: none;
						box-shadow: 0 0 5px $input_border_color;
						border: 1px solid #66afe9;
					}
				}

			}

			.main {
				textarea {
					font-size: 14px;
					height: 280px;
					margin-top: 10px;
					padding: 5px;
				}

				textarea:focus {
					outline: none;
					box-shadow: 0 0 5px $input_border_color;
					border: 1px solid #66afe9;
				}
			}
		}
	}
</style>



