<template>
	<div class="wrappers" role="navigation">
		<!-- bootstrap navbar -->
		<nav class="navbar navbar-default">
			<div class="container-fluid">
				<!-- 移动端 -->
				<div class="navbar-header">
					<button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1"
					 aria-expanded="false">
						<span class="sr-only">Toggle navigation</span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
					</button>
					<span class="login" v-show="!this.$store.state.userName"><span @click="login">登陆</span> || <span @click="registe" >注册</span></span>
					<span class="login" v-show="this.$store.state.userName"><span>{{this.$store.state.userName}}</span> || <span @click="logout" class="logout">注销</span></span>
				</div>
				<div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
					<ul class="nav navbar-nav navbar-right">
						<li @click="clickMark"><a href="#">新建</span></a></li>
						<!-- 筛选 -->
						<li class="dropdown">
							<a href="#" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">{{label}}
								<span class="caret"></span></a>
							<ul class="dropdown-menu">
								<li @click="clickFilter('全部')"><a href="#">全部<span>{{this.Length}}</span></a></li>
								<li role="separator" class="divider"></li>
								<li @click="clickFilter('生活')"><a href="#">生活<span>{{this.liveLength}}</span></a></li>
								<li @click="clickFilter('学习')"><a href="#">学习<span>{{this.studyLength}}</span></a></li>
								<li @click="clickFilter('工作')"><a href="#">工作<span>{{this.workLength}}</span></a></li>
							</ul>
						</li>
						<!-- 排序 -->
						<li @click="changeUp"><a href="#">按创建时间 <span class="glyphicon glyphicon-arrow-up" v-show="up"></span><span class="glyphicon glyphicon-arrow-down"
								 v-show="!up"></span></a></li>
						<!-- 查询 -->
						<form class="navbar-form navbar-left" role="search">
							<div class="form-group">
								<input type="text" class="form-control" placeholder="Search" @input="inputChange" v-model="inputText">
							</div>
						</form>
					</ul>
				</div>
			</div>
		</nav>
	</div>
</template>

<script>
	import 'bootstrap/dist/js/bootstrap.min.js';
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				label: '全部',
				sort: '按创建时间',
				up: false, //排序是升序还是降序
				inputText: ''
			}
		},
		methods: {
			//vuex中的方法
			...mapMutations([
				'setMainType',
				'addMemoItem',
				'setShowEdit',
				'deleteMemoItem',
				'setItemContent',
				'filterMemoItem',
				'sortMomeItem',
				'setIsNew',
				'setFilter',
				'setLogin',
				'setUserName',
				'setToken'
			]),

			clickMark() {
				this.setIsNew(true) //表示这是新建
				this.$store.commit('setShowEdit', true)
				this.$store.commit('setMainType', 1)
				var reset = {
					type: '', //markdown? poor?
					title: '',
					label: '', //分类
					time: '', //创建时间
					content: '' //内容
				}
				this.setItemContent(reset)
			},
			clickPoor() {
				this.$store.commit('setShowEdit', true)
				this.$store.commit('setMainType', 2)
				console.log('clickpoor', this.$store.state.mainType)

				// var canvasEle = $('.c')
				// 初始化 context
				let ctx = canvasEle.getContext('2d');
				let selectedColor = null;
				let hasOnGoingStroke = false;
				let prevStatusStack = [];
				let futureStatusStack = [];
				let $canvas = $(canvasEle);
			},
			//筛选选项
			clickFilter(data) {
				this.label = data
				console.log(data)
				this.filterMemoItem(data)
			},
			//改变排序
			changeUp() {
				this.up = !this.up
				this.sortMomeItem(this.up)
			},
			//文字筛选
			inputChange() {
				console.log(this.inputText)
				this.setFilter(this.inputText)
			},
			//登陆
			login(){
				this.setLogin(1)
			},
			//注册
			registe(){
				this.setLogin(2)
			},
			//注销
			logout(){
				this.setUserName('')
				this.setToken('')
				this.$message1('注销成功!','success')
			}

		},
		computed: {
			...mapState([
				'isShowEdit',
				'mainType',
				'memoItem',
				'itemContent',
				'isNew',
				'isLogin',
				'userName'
			]),
			//分类是学习的数量
			studyLength: function() {
				var length = JSON.parse(localStorage.memoItem).filter((item) => {
					return item.label == "学习"
				})
				return length.length
			},
			workLength: function() {
				var length = JSON.parse(localStorage.memoItem).filter((item) => {
					return item.label == "工作"
				})
				return length.length
			},
			liveLength: function() {
				var length = JSON.parse(localStorage.memoItem).filter((item) => {
					return item.label == "生活"
				})
				return length.length
			},
			Length: function() {
				return this.studyLength + this.workLength + this.liveLength
			}
		},
		mounted() {},

	}
</script>

<style lang="scss" scoped="scoped">
	@import 'bootstrap/dist/css/bootstrap.min.css';
	@import '../assets/style/sass.scss';
	
	.login{
		position: absolute;
		top: 14px;
		color: #77778c;
		span:hover{
			cursor: pointer;
		}
		.logout{
			color: #dd001b;
			font-weight: 600;
		}
	}
	
</style>



